@use "font" as *;
@use "mixins" as *;

.panel {
  position: sticky;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: var(--color-text-alt);
  padding: var(--container-padding);
  // border-radius: var(--safe-area-inset-top) env(safe-area-inset-right, 20px) env(safe-area-inset-bottom, 20px) env(safe-area-inset-left, 20px);
  box-shadow: 0 5px 12px color-mix(in srgb, var(--color-text) 25%, #0000);
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;

  &__item {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    --icon-size: 40;
    @extend %font-2;
    @include hoverType2;
  }
}